export default {
	 name:'step',
	 props:{
		 
		 step_desc:{
			 type:String,
			 default:'',
			 
		 },
		 step_index:{
			 type:Number,
			 default:1
		 }
	 },
	data(){
		return {
			//img_temp:'',
			desc:'',
			imgList:[],
			step_img_url:[],
			step_input:0,
			step_input_title:[]
		}
	},
	methods:{
		addInput(step){
			this.step_input = this.step_input+1;
			
		},
		ChooseImage(type,step) {
			
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: (res) => {
						if (this.imgList.length != 0) {
							this.imgList = this.imgList.concat(res.tempFilePaths)
						} else {
							this.imgList = res.tempFilePaths
						}
						console.log(res.tempFilePaths)
						uni.uploadFile({
						            url: 'http://help.ouyang.wiki/api/uploads', //仅为示例，非真实的接口地址
						            filePath: res.tempFilePaths[0],
									header: {
										'Access-Control-Allow-Origin':'*',
										'Access-Control-Allow-Credentials':'true',
									},  
						            name: 'file',
						            formData: {
						                'user': 'test'
						            },
						            success: (uploadFileRes) => {
										var res = JSON.parse(uploadFileRes.data)
										const step_imgs = JSON.stringify({step:step,step_img:res.path}) 
										//res.
										if (this.step_img_url.length != 0) {
											this.step_img_url = this.step_img_url.concat(res.path)
										} else {
											this.step_img_url.push(res.path)
										}
						                console.log(this.step_img_url);
						            }
						        });
					}
				});
			
			
		},
		ViewImage(e) {	
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			this.imgList.splice(e.currentTarget.dataset.index, 1)
			/* uni.showModal({
				title: '',
				content: '确定要删除这段回忆吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						
					}
				}
			}) */
		},
		textareaBInput(e) {
			this.textareaBValue = e.detail.value
		}
	}
}
